.modal{position:fixed;top:0;bottom:0;right:0;left:0;background:rgba(0,0,0,0);z-index:10;display:none}.modal.show{display:block}.modal>div{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);width:200px;height:200px;background-color:black;color:white;padding:24px;display:flex;flex-direction:column;align-items:center;justify-content:center;border-radius:8px;border:3px solid #1073C4}body{display:flex;justify-content:center;background-color:#000000}#main{display:flex;margin:0 auto;color:white}.status-bar{width:200px;height:680px;display:flex;flex-direction:column;padding:24px;margin:16px;margin-top:40px;border-radius:8px;border:3px solid #1073C4}.title{margin:0 auto}.title>h1{font-family:Verdana, Geneva, Tahoma, sans-serif;font-weight:700;margin:0}.next-canvas-container{width:132px;height:132px;border-radius:8px;border:3px solid #1073C4;display:flex;align-items:center;justify-content:center}.next-container{display:flex;align-items:center;justify-content:center;flex-direction:column;margin-bottom:16px}.container{margin:0 auto;margin-bottom:8px}.holder{text-align:center}h2{font-size:1.7rem;font-weight:200;margin:0 0 8px 0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif}p{text-align:center;font-size:0.8rem;font-weight:200;margin:0 0 8px 0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif}p>span{font-weight:bolder;font-size:0.85rem}button{font-size:1rem;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;padding:8px 16px;border-radius:8px;border:3px solid #1073C4;color:white;background-color:black;margin-bottom:8px}button:hover{background-color:#333333}button:disabled{color:gray;background-color:#333333}
